import React, {Component} from 'react';
import '../App.css';
import {Search} from './Search';
import {Avatar} from './Avatar';

class MenuPrincipal extends Component {
    state = {
        value: 0,
    };

    handleChange = (event, value) => {
        this.setState({value});
    };

    render() {
        const {value} = this.state;

        return (
            <div className="menu-fixed">
                <div className=".menu-fixed-container" style={{top: '0px', position: "relative"}}>
                    <div className="menu-fixed-main">
                        <a className="logo icon-logoUpdate active" href="/browse"></a>
                        <ul className="menu-fixed-links" role="navigation">
                            {this.props.lista.map(item => <li key={item} className="menu-fixed-links-item"><a className="menu-link"
                                                                                                   role="button"
                                                                                                   aria-haspopup="true">{item}</a>
                            </li>)}
                        </ul>
                    </div>
                </div>
            </div>
        );
    }
}

export default MenuPrincipal;



