import React from 'react';

const imageUrl = 'https://image.tmdb.org/t/p/';
const size = 'w500';
const TRUNCATE_LENGTH = 100;

const MovieDetails = (movie) =>{
    return(
    <div
        className="carousel-item"
        style={{
            backgroundColor: '#202020',
            backgroundImage: `url(${imageUrl}${size}${movie.movie.backdrop_path})`
        }}
    >
        <a href="#" >
            <div className="card-title">{movie.title}</div>
            <div className="card-info">
                <div className="card-year">{movie.movie.release_date ? movie.movie.release_date.split('-')[0] : null}</div>
                <div className="card-rating">{movie.movie.vote_average}</div>
                <span className="card-description">{movie.movie.overview ? movie.movie.overview.substring(0, TRUNCATE_LENGTH) + '...' : 'No h descrio para esse filme'}</span>
            </div>
        </a>
        <div class="slider-item slider-item-0">
            <div class="title-card-container">
                <div id="title-card-2-0" class="title-card">
                    <div class="ptrack-content" data-ui-tracking-context="%7B%22list_id%22:%224af3937e-e0da-4397-893f-c0115d78e952_12439190X54XX1524312268619%22,%22location%22:%22homeScreen%22,%22rank%22:0,%22request_id%22:%2274d3cad2-4047-435e-851a-cb5aef0b7548-34463355%22,%22row%22:2,%22track_id%22:15035895,%22video_id%22:80007945,%22image_key%22:%22sdp,0%7Ccbdc3391-2d36-11e8-a4ab-1220672dc0cc%7Cpt-BR%22,%22supp_video_id%22:1,%22lolomo_id%22:%224af3937e-e0da-4397-893f-c0115d78e952_ROOT%22,%22maturityMisMatchEdgy%22:false,%22maturityMisMatchNonEdgy%22:false,%22uuid%22:%22adbe4084-fedb-42d3-9410-9a50ef8e15d4%22,%22appView%22:%22boxArt%22,%22usePresentedEvent%22:true%7D" data-tracking-uuid="adbe4084-fedb-42d3-9410-9a50ef8e15d4">
                        <a href="/watch/80007945?tctx=2%2C0%2C%2C%2C" aria-label="Chef's Table" tabindex="0" aria-hidden="false" class="slider-refocus">
                            <div class="video-artwork is-loaded lazy-background-image" data-src="false" style="background-image: url(&quot;https://occ-0-539-1380.1.nflxso.net/art/02952/65a02a5c2b7a71c2ad38ad6a851ddc0d06602952.jpg&quot;);"></div>
                        </a>
                        <div class="video-preload-title" aria-hidden="true">
                            <div class="video-preload-title-label">Chef's
                                Table
                            </div>
                        </div>
                    </div>
                    <span></span></div>
            </div>
        </div>
    </div>
);}

export { MovieDetails };


