// src/redux/reducers/movies.js
const initialState = {
    movies: [],
    moviesTheater:[],
    moviesPopular:[],
    movie: {}
}
export default (state=initialState, action) => {
    switch (action.type) {
        case 'GENDER_MOVIES' :
            return {
                ...state,
                movies: action.movies
            }
        case 'HOME_MOVIES':
            return {
                ...state,
                moviesTheater: action.moviesHome.moviesTheater,
                moviesPopular: action.moviesHome.moviesPopular
            }
        case 'POPULAR_MOVIES':
            return {
                ...state,
                moviesPopular: action.moviesPopular
            }
        case 'SEARCH_MOVIE':
            return{
                ...state,
                movie: action.movie
            }
        default:
            return state
    }
}


