// src/redux/actions/actions.js
/** */
import axios from 'axios'
const api = "https://api.themoviedb.org/3"
const apiKey = '5a9940cf4d7f375315e37a7f90afdd98';
const defaultOptions= 'language=pt-BR';
// busca filme por genero
export const getByGenrer = (gender)=>{
    return (dispatch)=>{axios.get(`${api}/genre/movie/list?api_key=${apiKey}&${defaultOptions}`)
            .then((res) => {
                let movies = res.data
                dispatch({type:'GENDER_MOVIES', movies})
            }).catch((err) => {
            console.log(err)
        })
    }
}
//busca filmes que sero apresentados na home
export const getHomeMovies=()=> {
    return(dispatch)=> {
        let moviesHome={
            moviesTheater:[],
            moviesPopular:[]
        };
        axios.get(`${api}/movie/now_playing?api_key=${apiKey}&${defaultOptions}`)
            .then((res) => {
                moviesHome.moviesTheater = res.data.results;
                axios.get(`${api}/movie/popular?api_key=${apiKey}&${defaultOptions}`)
                    .then(res => {
                        moviesHome.moviesPopular = res.data.results;
                        dispatch({type: 'HOME_MOVIES', moviesHome});
                    });
            });


    }
}
// busca filmes populares
export const getMostPopular = ()=> {
    return (dispatch)=> {
        axios.get(`${api}/movie/popular?api_key=${apiKey}&${defaultOptions}`)
            .then(res => {
                let moviesPopular = res.data.results;
                dispatch({type: 'POPULAR_MOVIES', moviesPopular});
            });
    }
}
// busca filmes de acordo com o usuario
export const search = (query)=> {
    return (dispatch) => {
        axios.get(`${api}/search/movie?api_key=${apiKey}&query=${query}&${defaultOptions}`)
            .then(res => res.json())
            .then(data => {
                /* In cases when no movies were found. Try search for movies known for */
                if (!data.total_results) {
                    return axios(`${api}/search/person?api_key=${apiKey}&query=${query}&${defaultOptions}`)
                        .then(res => res.json())
                        .then(data => {
                            if (!data.total_results) {
                                return [];
                            }
                            let movies = data.results
                                .map(result => result.known_for) /* get only array of known_for */
                                .reduce((a, b) => [...a, ...b]);
                            /* reduce many arrays into only one */

                            dispatch({type: 'SEARCH_MOVIE', movies});
                        })
                }

                /* In cases when movie were searched for return it */
                return data.results;
            });
    }
}


