import React, {Component} from 'react';
import '../App.css';
import {connect} from 'react-redux';
import MenuPrincipal from '../components/MenuPrincipal';
import {Jumbo} from '../components/Jumbo';
import {Carousel} from '../components/Carousel';
import Footer from '../components/Footer'
import {getHomeMovies} from './../redux/actions/actions'
import {MovieDetails} from "../components/MovieDetails";

const mapStateToProps = state => {
    return {
        moviesTheater: state.movies.moviesTheater,
        moviesPopular: state.movies.moviesPopular,
        movie:state.movies.movie,
    }
}
class Home extends Component {
    componentWillMount() {
        this.props.getHomeMovies();
    }
    render() {
        const inTheater = this.props.moviesTheater.map((movie) => <MovieDetails key={movie.id} movie={movie}/>);
        const popular = this.props.moviesPopular.map((movie) => <MovieDetails key={movie.id} movie={movie}/>);
        const jumbo =this.props.movie;
        console.log(jumbo);
        return (
            <div className="home">
                {<Jumbo key={jumbo.id} movie={jumbo}/>}
                <MenuPrincipal lista={['inicio', 'minha lista']}/>
                <Carousel className="rowContent" title="Nos Cinemas">
                    {inTheater}
                </Carousel>
                <Carousel className="rowContent" title="Populares">
                    {popular}
                </Carousel>
                <Footer/>
            </div>
        );
    }
}
export default connect(mapStateToProps, { getHomeMovies })(Home);


