import React, {Component} from 'react';


class Footer extends Component {
    render() {
        return (
            <div role="contentinfo" className="member-footer">
                <ul className="member-footer-links">
                    <li className="member-footer-link-wrapper"><a className="member-footer-link"
                                                                  href="/browse/subtitles"><span
                        className="member-footer-link-content">Idioma e legendas</span></a></li>
                    <li className="member-footer-link-wrapper"><a className="member-footer-link"
                                                                  href="/browse/audio-description"><span
                        className="member-footer-link-content">Audiodescrio</span></a></li>
                    <li className="member-footer-link-wrapper"><a className="member-footer-link"
                                                                  href="https://help.netflix.com/"><span
                        className="member-footer-link-content">Centro de ajuda</span></a></li>
                    <li className="member-footer-link-wrapper"><a className="member-footer-link" href="/redeem"><span
                        className="member-footer-link-content">Carto pr-pago</span></a></li>
                    <li className="member-footer-link-wrapper"><a className="member-footer-link"
                                                                  href="https://media.netflix.com/"><span
                        className="member-footer-link-content">Imprensa</span></a></li>
                    <li className="member-footer-link-wrapper"><a className="member-footer-link"
                                                                  href="http://ir.netflix.com/"><span
                        className="member-footer-link-content">Relaes com investidores</span></a></li>
                    <li className="member-footer-link-wrapper"><a className="member-footer-link"
                                                                  href="https://jobs.netflix.com/"><span
                        className="member-footer-link-content">Carreiras</span></a></li>
                    <li className="member-footer-link-wrapper"><a className="member-footer-link"
                                                                  href="https://help.netflix.com/legal/termsofuse"><span
                        className="member-footer-link-content">Termos de uso</span></a></li>
                    <li className="member-footer-link-wrapper"><a className="member-footer-link"
                                                                  href="https://help.netflix.com/legal/privacy"><span
                        className="member-footer-link-content">Privacidade</span></a></li>
                    <li className="member-footer-link-wrapper"><a className="member-footer-link" href="/Cookies"><span
                        className="member-footer-link-content">Preferncias de cookies</span></a></li>
                    <li className="member-footer-link-wrapper"><a className="member-footer-link"
                                                                  href="https://help.netflix.com/en/node/2101"><span
                        className="member-footer-link-content">Informaes corporativas</span></a></li>
                    <li className="member-footer-link-wrapper"><a className="member-footer-link"
                                                                  href="https://help.netflix.com/contactus"><span
                        className="member-footer-link-content">Entre em contato</span></a></li>
                </ul>
                <div className="member-footer-service">
                    <button className="service-code">Cdigo do servio</button>
                </div>
            </div>);
    }
}

export default Footer;


